.hidden-menu {
  display: block;
  position: fixed;
  list-style:none;
  padding: 10px;
  margin: 0;
  box-sizing: border-box;
  width: 200px;
  background-color: #eee;
  height: 100%;
  top: 0;
  right: -200px;
  transition: right .2s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.hidden-menu-ticker {
  display: none;
}

.btn-menu {
  padding: 5px;
  position: fixed;
  top: 30px;
  right: 30px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 30px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.btn-menu span {
  display: block;
  height: 5px;
  background-color: #444;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.btn-menu span.first {
  margin-top: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu {
  right: 160px;
}

.hidden-menu-ticker:checked ~ .hidden-menu {
  right: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu span.first {
  transform: rotate(45deg);
  top: 10px;
}

.hidden-menu-ticker:checked ~ .btn-menu span.second {
  opacity: 0;
}

.hidden-menu-ticker:checked ~ .btn-menu span.third {
  transform: rotate(-45deg);
  top: -10px;
}

@media (min-width: $tablet-width) {
  .hidden-menu-wrapper {
    display: none;
  }
}

@media (min-width: $desktop-width) {
  .hidden-menu-wrapper {
    display: none;
  }
}
