.site-list__item a {
  color: black;
  font-size: 16px;
  text-decoration: none;
}

.site-list__item {
  padding-top: 20px;
  padding-left: 45px;
}

@media (min-width: $tablet-width) {
  .site-list__item {
    padding-bottom: 8px;

    border-top: none;
  }

  .site-list__item {
    padding-left: 10px;
  }
}

@media (min-width: $desktop-width) {
  .site-list__item a[href] {
    &:hover {
      opacity: 0.5;
    }
  }

  .site-list__item a {
    font-size: 20px;
  }
}
